<template>
    <Row class="sidebarContents">
      <div class="logo" v-on:click="goHome"><h1>TURF</h1></div>
      <Col span="20" offset="3" class="sideNav">
        <router-link to="/getting-started" tag="li" class="menuItem heading">Getting Started</router-link>

        <input id="sidebarFilter" placeholder="Search modules" v-on:keyup='filterList'>
        <ul class="turfModules">
          <li v-for="module in displayedModules"
            class="menuItem"
            v-bind:class="{heading: module.isHeading}" 
            v-on:click="clickModule"
            >{{module.name}}</li>
        </ul>
      </Col>
    </Row>
</template>

<script>
import {Row, Col} from 'iview/src/components/grid'

export default {
  name: 'Sidebar',
  props: ['modules'],
  components: {
    Row,
    Col
  },
  computed: {
    displayedModules: function () {
      return this.modules.filter(function (module) {
        return module.hidden === false
      })
    }
  },
  methods: {
    goHome: function () {
      this.$router.push('/')
    },
    clickModule (e) {
      if (e.target.classList.value.includes('heading')) return
      this.$router.push('docs')
      window.scrollTo(0, 0)
      this.$emit('changeModule', e.target.innerText)
    },
    filterList (e) {
      this.$emit('filterChanged', e.target.value.toUpperCase())
    }
  }
}
</script>

<style lang="less">
  @import "../styles/variables.less";

.sidebarContents{
  top: 0;
  bottom: 0;
  position: fixed;
  overflow-x: hidden;
  overflow-y: scroll;
  width: inherit;
  background-color: @lightGrey;

  .logo{
    height: 120px;
    width: 100%;
    background-color: #2ECC71;
    margin-bottom: 20px;
    cursor: pointer;
    h1 {
      font-size: 2rem;
      color: white;
      text-align: center;
      letter-spacing: 0.4rem;
      padding-top: 30px;
      font-weight: 600;
    }
  }

  .sideNav {
    .heading{
      font-weight: 700;
      text-transform: uppercase;
      margin-top: 10px;
      font-size: 14px;
    }

    #sidebarFilter{
      margin: 30px 0px 10px -10px;
      font-weight: 0.9rem;
      width: 100%;
      padding: 5px;
      border: solid 3px #efefef;
      color: #656565;
      transition: 0.3s;
      &:focus {
        outline: none;
        border: solid 3px #cccccc;      
      }
    }

    .menuItem {     
      margin: 5px 0px;
      color: #656565;
      text-decoration: none;
      cursor: pointer;
      list-style-type: none;
    }

    .heading {
      margin-left: -5px;
    }
  }
}

</style>



